@use "global/bootstrap";

.services {
  @include bootstrap.reset-list;
  padding: 23px 18px 46px;
  background-color: bootstrap.$main-bgColor;

  @media (min-width: bootstrap.$tablet-width) {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    min-height: 330px;
    padding: 0;
  }

  &__title {
    @include bootstrap.boldTitle;
    position: relative;
    color: bootstrap.$title-color;
    margin-top: 8px;
    margin-bottom: 16px;

    @media (min-width: bootstrap.$tablet-width) {
      margin-top: 100px;
      margin-bottom: 29px;
    }

    &--home::before {
      position: absolute;
      content: "";
      background-image: url("../img/icon-house.svg");
      background-repeat: no-repeat;
      width: 77px;
      height: 72px;

      top: 1px;
      left: -46px;

      @media (min-width: bootstrap.$tablet-width) {
        width: 80px;
        height: 68px;
        top: -26px;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }

    &--food::before {
      position: absolute;
      content: "";
      background-image: url("../img/icon-burger.svg");
      background-repeat: no-repeat;
      width: 40px;
      height: 35px;

      top: 1px;
      left: -47px;

      @media (min-width: bootstrap.$tablet-width) {
        width: 80px;
        height: 68px;
        top: -26px;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }

    &--souvenirs::before {
      position: absolute;
      content: "";
      background-image: url("../img/icon-gift.svg");
      background-repeat: no-repeat;
      width: 32px;
      height: 38px;

      top: 1px;
      left: -43px;

      @media (min-width: bootstrap.$tablet-width) {
        width: 66px;
        height: 75px;
        top: -26px;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }

  &__description {
    @include bootstrap.normalTitle;
    color: bootstrap.$secondary-color;
  }

  &__item {
    text-align: left;
    margin-left: 47px;

    @media (min-width: bootstrap.$tablet-width) {
      text-align: center;
      margin-left: 0;
      padding: 60px 33px;
    }

    @media (min-width: bootstrap.$desktop-width) {
      padding: 69px 50px;
    }

    &:not(:last-child) {
      margin-bottom: 25px;

      @media (min-width: bootstrap.$tablet-width) {
        margin-bottom: 0;
      }
    }
  }
}
